:root {
    --black: #333;
    --gold: goldenrod;
    --red: firebrick;
    --primary-color: var(--black);
    --primar-bgcolor: #eee;
    --accent-color: #5e00ff;
    --accent-bgcolor: #ff6f00;
    --default-length: .5rem;
    --default-min-length: .1rem;
    --default-border: 1px solid var(--accent-bgcolor);
    --default-margin: calc(.2 * var(--default-length));
    --default-padding: calc(.2 * var(--default-length));

    --muted-text-color: #6c757d;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-width: 600px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::after, *::before {
    box-sizing: border-box;
}

/* hacks */
nav.nav.nav-tabs {
    margin: 1rem 0;
}

a.nav-item.nav-link.active {
    border-bottom: thick solid var(--accent-bgcolor);
}
